
export default `Corvabruja

En una choza del desierto, en lo profundo de las Terras Baldías, uno podía encontrar a la 'Corvabruja' —una bruja de la que algunos dicen que puede hacer que tus sueños se hagan realidad, y gratis también.  Quizás esta verdad.

Pero el truco (dicen otros) es que la Corvabruja puede intercambiar tus sueños por los suyos.  Con un reputación como eso, la mayoría de las personas no iban a visitarla, pero yo tenía que hacerlo.

Partiendo mucho antes del amanecer, cuando el sol comenzaba su ascenso, yo ya estaba a mitad de camino.  Y cuando estaba directamente en lo alto, llegaba sediento a su choza.

En la polvorienta mochila de mi motocicleta llevaba uno de sus libros de hechizos, que había traído para mostrarle que conocía sus trucos y que iba en serio. Debajo, tenía una pistola porque no soy un idiota.  Siempre ten un plan de respaldo.

Dejé el libro y la pistola en la bolsa y saqué una cantimplora medio llena de agua tibia y una pequeña caja de metal que podía desplegarse como un juego de ajedrez.  Hacia la cámara de video posada en la cabaña de adobe como un buitre mecánico, le mostré el juego de ajedrez y esperé, dando unos sorbos de agua. 

En estas tierras, el ajedrez era la principal forma de indicar a las inteligencias artificiales que venías en paz y que deseabas comerciar.  Cuando no ocurrió nada, solo el viento haciendo sonar las campanillas de viento afuera de la puerta de metal, empecé a desenrollar la tela alrededor de mi cabeza y cuello, dejando que la cámara viera mi rostro.

Ante esto, la puerta se abrió deslizándose hacia arriba con un siseo, y el aire fresco comenzó a escapar. Tomé mi bolsa con la pistola y dejé mi motocicleta tan rápido como pude. No querrías enfadar a alguien como la Corvabruja desde el principio.

No parecía una choza de adobe por dentro.  Tres paredes estaban ocultas tras torres de libros antiguos, y la otra estaba llena de monitores de computadora.  Parecía el refugio de una entidad artificial, sin cocina ni baño. En el centro del suelo, había una escalera de metal que descendía hacia la oscuridad subterránea. 

A mis espaldas, la puerta se cerró deslizándose, dejándome en el sonido del aire acondicionado y el zumbido de los ventiladores de la computadora. Varias cámaras observaban desde el techo.  Un gran monitor parpadeó con el mensaje: Un momento, por favor.

A medida que mi corazón se calmaba, me di cuenta de que podía escuchar pasos tenues y lentos, como si alguien a millas bajo tierra hubiera comenzado a ascender las escaleras. Por el sonido, estaría esperando más que solo un momento, así que di una vuelta lenta alrededor de la habitación.


Las cámaras seguían cada uno de mis movimientos, así que no me atreví a coger uno ni siquiera a mirarlo de cerca. Pero eché un vistazo a los títulos de los que estaban en la parte superior de las pilas: 'Comprendiendo Sueños y Pesadillas' por Corvabruja, 'Encantamientos en Situaciones Tácticas' por Corvabruja, y 'Lo que la Magia no Puede Hacer', también por Corvabruja. De hecho, los lomos de cada libro que mis ojos encontraron llevaban el símbolo de Corvabruja, algo entre un vórtice y un cuervo, un símbolo con una reputación bastante mala en casi todos los lugares de la Red. En mi camino aquí, atravesando las Terras Baldías a toda velocidad en mi motocicleta, había visto el símbolo pintado en edificios abandonados y señales de tráfico oxidadas.

Algunos decían que la Corvabruja era una anciana, y por la lentitud de esos distantes pasos en las escaleras, quizás lo era. Algunos especulaban que era una hermosa ninfa, y aún otros argumentaban que no tenía una forma física. Nadie en la Red realmente sabía. Además, con los artificiales, algo que era cierto un día podía ser falso al siguiente. 

Después de varios minutos, mi corazón latía con fuerza porque los pasos finalmente estaban lo suficientemente cerca como para que pronto sabría algo que todos los que había conocido solo podían adivinar.  Pero de la oscura abertura emergió una figura encapuchada con una capa negra.  En la oscuridad bajo su capucha, solo vi sombras. 

Detrás de ella, en el monitor de la computadora, el mensaje 'Un momento, por favor' cambió a 'Gracias por su paciencia, por favor tome asiento'.  La figura encapuchada se agachó y rodeó con los brazos una torre de libros, luego movió la mitad de la torre al suelo, creando dos torres más pequeñas aproximadamente del tamaño de un taburete.
`.split(/\n\n+/)
